@import url(https://fonts.googleapis.com/css2?family=Zen+Kurenaido&display=swap);
/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: 'Zen Kurenaido', sans-serif;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  3. Allow percentage-based heights in the application
*/
html,
body {
  height: 100%;
}
/*
  Typographic tweaks!
  4. Add accessible line-height
  5. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
  6. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/*
  7. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}
/*
  8. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/*
  9. Create a root stacking context
*/
#root {
  isolation: isolate;
}

.container{min-width:100vw;min-height:100vh;background:#344955;display:flex;justify-content:center;align-items:center;flex-direction:column;color:#fff}.container h1{font-size:4em;margin-bottom:24px}.container h3{text-transform:capitalize}.form{background:#fff;width:100%;max-width:720px;padding:24px;margin-bottom:24px;border-radius:7px;display:grid;grid-template-columns:1fr 1fr 150px;grid-gap:24px}.form__select{-webkit-appearance:none;-moz-appearance:none;appearance:none;padding:7px;border:none;border-radius:7px;background:#232e34;color:#fff;width:100%}.form__select-wrapper{position:relative}.form__select-wrapper::after{content:'';width:10px;height:10px;border-left:2px solid #fff;border-bottom:2px solid #fff;position:absolute;right:13px;top:50%;transform:translateY(-50%) rotate(-45deg)}.form__button{background:#232e34;border:none;color:#fff;border-radius:7px;cursor:pointer;padding:7px}.form__button:disabled{background:#eee;color:#777;cursor:not-allowed}

.scale-container{background:#fff;border:1px solid #000;padding:24px;width:1088px;color:#000;border-radius:7px}.fretboard{display:grid;grid-template-columns:repeat(13, 80px)}.fretboard__item{height:32px;border-top:1px solid #000;border-right:1px solid #000;position:relative;background:#ebe5d5}.fretboard__item:nth-of-type(n+66){border-bottom:none;border-left:none;border-right:none;background:transparent}.fretboard__item:nth-of-type(1),.fretboard__item:nth-of-type(14),.fretboard__item:nth-of-type(27),.fretboard__item:nth-of-type(40),.fretboard__item:nth-of-type(53),.fretboard__item:nth-of-type(66){border-right-width:7px;background:#fff}.fretboard__item-note{background:#8b4513;color:#fff;width:1.8em;height:1.8em;display:grid;place-items:center;border-radius:50%;position:absolute;left:50%;transform:translateX(-50%);top:-0.9em;font-size:0.9em}.fretboard__item--key .fretboard__item-note{background:#ff373d}.fretboard__marker{text-align:center}.fretboard:nth-of-type(2){margin-top:-12px;z-index:2}

